import React, {useEffect, useState} from 'react'
import './Main.css'
import {Link} from'react-router-dom'
import logo from '../assets/logoGet.svg'
import api from '../services/apis'
import matchImage from '../assets/itsamatch.png'

export default function Main({ match}){
    const [users, setUsers] = useState([]);
    const [matchDev, setMachDev] = useState(null)
     
    useEffect(()=>{
        async function loadUsers(){
            const response = await api.get('/emps',{
                headers: {user: match.params.id}
            })
            setUsers(response.data)
        }
        loadUsers();
    }, [match.params.id])

    return(
        <div className="main-container">
            <Link to="/">
                <img src={logo} className="logo" alt="Tindev"/>
            </Link>
            <ul>
                <li>
                        <footer>
                        <strong>Criar Vaga</strong>
                        <a href={`/criavaga/${match.params.id}`}>Cria vaga</a>
                        </footer>
                    </li>
            </ul>
            { users.length > 0 ? (
                <ul>
                {users.map(user => (
                    <li key={user._id}>
                        <img src={user.avatar} alt={user.name}/>
                        <footer>
                        <strong>{user.atuacao}</strong>
                            <strong>{user.name}</strong>
                            <p>{user.descricao}</p>
                            <p>{user.cidade}</p>
                            <a href={`/homevaga/${user._id}`}>Ver vaga</a>
                        </footer>
                    </li>
                ))}
            </ul>
            ) :(
                <div className="empty">Acabou</div>
            ) }

            {matchDev && (
                <div className="match-container">
                    <img src={matchImage} alt="itsMatch"/>
                    <img className="avatar" src={'localhost:3333/image/' + matchDev.avatar} alt="Avatar"/>
                    <strong>{matchDev.name}</strong>
                    <p>{matchDev.bio}</p>
                    <button type="button" onClick={() => setMachDev(null)}>Fechar</button>
                </div>
            )}
        </div>
    )
}